import loginApi from '@/api/login.js'
import { miniProApi } from '@/utils/mixins.js'
import Authorize from '@/pages/components/authorize/authorize'
export default {
	mixins:[ miniProApi ],
	components:{
		Authorize
	},
	data() {
		return {
			username: '',
			password: '',
		};
	},
	onLoad() {
		console.log("----------login页面 ----onload----")
	},
	methods:{
		// onComLoad
		async onComLoad() {
			console.log("----------login页面 ----onComLoad----")
			// 先判断 用户是否微信授权

				let isAuthorize = await this.getAuthorizeStatus("scope.userInfo", async () => {
					this.$store.dispatch("setAuthorizeState", true)												
					this.reLaunchPage("../find/find")
				}, async () => {
					// this.reLaunchPage("../find/find")												
					this.$store.dispatch("setAuthorizeState", false)
				})
				
				// if(isAuthorize){
				// 	// 已授权
				// 	// 页面跳转到find页面
				// 	this.reLaunchPage("../find/find")
				// 	this.$store.dispatch("setAuthorizeState", true)
				// 	
				// }else {
				// 	// this.$store.dispatch("setAuthorizeState", false)
				// }

		},
		login(){
			// debugger
			this.showLoading();
			if(this.username && this.password){
				// 调取注册/登录接口
				let params = {
					name: this.username,
					pwd: this.password
				}
				this.hideLoading()
				loginApi.register(params).then((res)=>{
					debugger
					console.log("调取注册/登录接口后返回到数据-----》",res)
					if(res && res.data.code === 1) {
						// 成功后 
						// debugger
						// 将用户信息存入到store 中
						if(res.data.user){
							this.$store.dispatch('setUserToken', res.data.user.token || '')								
						}
						this.switchPage('../find/find').then((res) =>{
							this.success('登录成功')
						}).catch(()=>{
							
						})
					}else {
						this.hideLoading();
					}
				})	
			}else {
				this.hideLoading();
				this.alert('用户名或密码不能为空')
			}
		}
	}
}
